.start_sticky .box_embed_video .embed-container-square {

    width: 320px;
    height: 256px;
    position: fixed;
    bottom: 36px;
    left: 20px;
    padding-bottom: 0;
    z-index: 9999;
    overflow: visible;

}

.start_sticky .box_embed_video .embed-container-square {
    iframe {
        width: 100%;
        height: 256px;
    }
}

